////// Packages //////////////////
import { FC } from 'react';
import className from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Card, CardBody } from 'reactstrap';
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { FalconColors } from 'components/falconTypes';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
  media: {
    icon: IconProp;
    className?: string;
    color?: FalconColors
  };
  title: string;
  description: string;
}
const CardService: FC<Props> = ({ media, title, description, children }) => (
  <Card className="card-span h-100">
    <div className="card-span-img">
      <FontAwesomeIcon
        icon={media.icon}
        className={className({ [`text-${media.color}`]: media.color }, media.className)}
      />
    </div>
    <CardBody className="pt-6 pb-4">
      <h5 className="mb-2">{title}</h5>
      {description && <p>{description}</p>}
      {children}
    </CardBody>
  </Card>
);

export default CardService;
