////// Packages //////////////////
import React, { FC } from 'react';
import classNames from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Row, Col } from 'reactstrap';
import { FalconColors } from 'components/falconTypes';
import { IconProp } from '@fortawesome/fontawesome-svg-core';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


interface Props {
  icon: IconProp;
  iconText: string;
  color: FalconColors;
  title: string;
  description: string;
  image: string;
  inverse?: boolean;
  children?: React.ReactNode
}

const Process: FC<Props> = ({ icon, iconText, color, title, description, image, inverse, children }) => {
  return (
    <Row className="flex-center mt-8">
      <Col md lg={5} xl={4} className={classNames('pl-lg-6', { 'order-md-2': inverse })}>
        <img className="img-fluid px-6 px-md-0" src={image} alt="" />
      </Col>
      <Col md lg={5} xl={4} className="mt-4 mt-md-0">
        <h5 className={`text-${color}`}>
          <FontAwesomeIcon icon={icon} className="mr-2" />
          {iconText}
        </h5>
        <h3>{title}</h3>
        <p>{description}</p>
        {children}
      </Col>
    </Row>
  );
};

export default Process;
