////// Packages //////////////////
import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Collapse, Container, Nav, Navbar, NavbarBrand, NavbarToggler } from 'reactstrap';

////// Local Components //////////
import NavbarTopDropDownMenus from './components/NavbarTopDropDownMenus';
import LandingRightSideNavItem from './components/LandingRightSideNavItem';

////// Global Components /////////

////// CommonJS Helpers //////////
import handleNavbarTransparency from 'helpers/handleNavbarTransparency';
import { topNavbarBreakpoint } from 'environ';
import { useDispatch } from 'react-redux';
import { settings } from 'store/ducks/settings';
import useSetting from 'store/hooks/settings/useSettings';

////// Custom Hooks //////////////

////// Thunks ////////////////////


const NavbarStandard = () => {
  const dispatch = useDispatch();
  const navbarCollapsed = useSetting('navbarCollapsed')

  useEffect(() => {
    window.addEventListener('scroll', handleNavbarTransparency);
    return () => window.removeEventListener('scroll', handleNavbarTransparency);
  }, []);

  return (
    <Navbar
      dark
      fixed="top"
      expand={topNavbarBreakpoint}
      className={classNames('navbar-standard navbar-theme', {
        'bg-dark': !navbarCollapsed
      })}
    >
      <Container>
        <NavbarBrand className="text-white" tag={Link} to="/">
          happin
        </NavbarBrand>
        <NavbarToggler onClick={() => dispatch(settings.actions.setNavbarCollapsed(!navbarCollapsed)) } />
        <Collapse isOpen={!navbarCollapsed} navbar className="scrollbar">
          <Nav navbar>
            <NavbarTopDropDownMenus />
          </Nav>
          <LandingRightSideNavItem />
        </Collapse>
      </Container>
    </Navbar>
  );
};

export default NavbarStandard;
