
import { batch, DefaultRootState } from 'react-redux';
import { MOrganization, MPerson, MUser } from 'store/types/models/id';
import { ThunkAction } from 'redux-thunk';
import { Thunk } from 'store/store';
import { organizations, people, users } from 'store/ducks/id';
import loginDuck from 'store/ducks/login';
import { $authentication } from 'environ';
type LoginReturn = {
    success: boolean;
    isLoggedIn: boolean;
    user: MUser;
    person: MPerson;
    organization: MOrganization;
    verify?: boolean;
} & {verify?: true} & {error?: string, success?: false}

export const login: Thunk<LoginReturn | null> = (email: string, password: string) => {
    return async dispatch => {
        // const message = { 
        //     auth: {
        //         login: {
        //             email,
        //             password
        //         }
        //     }
        // }
        // return dispatch(wsSend(message));
        const res = await fetch($authentication + '/login', {
            credentials: "include",
            method: "POST",
            body: JSON.stringify({email, password}),
            headers: {
                "Content-Type": "application/json"
            }
        })
        const response = await res.json();
        if ( response?.success ) {
            const {user, person, organization } = response;
            batch(() => {
                dispatch( loginDuck.actions.setLogin(response.isLoggedIn))
                dispatch( loginDuck.actions.onLogin(user._id, person._id, organization._id) );
                dispatch( users.upsert(user, user._id));
                dispatch( people.upsert(person, person._id));
                dispatch( organizations.upsert( organization, organization._id ))
            })
        }
        return response;
    }
}

interface LogoutReturn {
    success?: boolean;
    isLoggedIn?: boolean;
}

export const logout = ():
    ThunkAction<Promise<LogoutReturn | null>, DefaultRootState, any, any> => {
        return async dispatch => {

            const res = await fetch($authentication + '/logout', {
                credentials: "include",
                method: "POST"
            })
            const response = await res.json();
            if ( response?.success ) {
                dispatch( loginDuck.actions.setLogin( false ) );
                dispatch( {type: 'RESET'} )
            }
            return response;


            // const message = { 
            //     auth: {
            //         logout: true
            //     }
            // }
            // return dispatch(wsSend(message));
        }
    }

const thunks = {
    logout,
    login
}

export default thunks;