////// Packages //////////////////
import React, { Fragment, useState, useEffect, FC } from 'react';
import { Spinner } from 'reactstrap';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Button, CustomInput, Form, FormFeedback, FormGroup, Input, Label } from 'reactstrap';


////// Local Components //////////
import SocialAuthButtons from './SocialAuthButtons';

////// Global Components /////////
import Divider from '../../components/Divider';
import withRedirect from '../../hoc/withRedirect';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
  setRedirect: (redirect: boolean)=>void;
  setRedirectUrl: (redirectUrl: string)=>void;
  hasLabel: boolean;
}

const RegistrationForm: FC<Props> = ({ setRedirect, setRedirectUrl, hasLabel=false }) => {
  // State
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [organizationName, setOrganizationName] = useState('');
  const [email, setEmail] = useState('');
  const [emailTaken, setEmailTaken] = useState(false);
  const [emailValid, setEmailValid] = useState(false);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isAccepted, setIsAccepted] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);
  const [loading, setLoading] = useState(false);

  // Handler
  const handleSubmit = async (e: any) => {
    e.preventDefault();
    try {
      setLoading(true);
      const response = await fetch('https://happin.design/authentication/signup', {
          credentials: "include",
          method: "POST",
          body: JSON.stringify({email, password, firstName, lastName, organizationName}),
          headers: {
              "Content-Type": "application/json"
          }
      })
      const res = await response.json();
      if(res?.success) {
        toast.success(res.successMessage);
        setRedirectUrl(`/auth/confirm-mail/verify/${ encodeURIComponent(email) }`);
        setRedirect(true);
      } else if (res?.error === 'email exists') {
        setEmailTaken(true);
      }
    }
    catch(err) {
      console.log(err)
    }
    finally {
      setLoading(false)
    }
  };

  useEffect(() => {
    setEmailTaken(false);
    setEmailValid(
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
        .test(email)
    )
  }, [email]);

  useEffect(() => {
    setRedirectUrl(`/auth/login`);
  }, [setRedirectUrl]);

  const passwordValid = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,15}$/.test(password);

  useEffect(() => {
    setIsDisabled(
      !firstName ||
      !lastName ||
      !organizationName ||
      !email || 
      !password || 
      !confirmPassword || 
      !isAccepted || 
      password !== confirmPassword ||
      !passwordValid
    );
  }, [
    firstName, 
    lastName, 
    organizationName, 
    email, 
    password, 
    confirmPassword, 
    isAccepted
  ]);

  if (loading) {
    return <Spinner color="info" />
  } else {
    return (
      <Form onSubmit={handleSubmit}>
        <div className="form-row">
          <FormGroup className="col-md-6">
            {hasLabel && <Label>First Name</Label>}
            <Input
              placeholder={!hasLabel ? 'First Name' : ''}
              value={firstName}
              onChange={({ target }) => setFirstName(target.value)}
              type="text"
            />
          </FormGroup>
          <FormGroup className="col-md-6">
            {hasLabel && <Label>Last Name</Label>}
            <Input
              placeholder={!hasLabel ? 'Last Name' : ''}
              value={lastName}
              onChange={({ target }) => setLastName(target.value)}
              type="text"
            />
          </FormGroup>
        </div>
        <FormGroup>
          {hasLabel && <Label>Organization Name</Label>}
          <Input
            placeholder={!hasLabel ? 'Organization Name' : ''}
            value={organizationName}
            onChange={({ target }) => setOrganizationName(target.value)}
            type="text"
          />
        </FormGroup>
        <FormGroup>
          {hasLabel && <Label>Email address</Label>}
          <Input
            placeholder={!hasLabel ? 'Email address' : ''}
            value={email}
            onChange={({ target }) => setEmail(target.value)}
            type="email"
            invalid={emailTaken}
            valid={emailValid}
          />
          {emailTaken && <FormFeedback>That email address is already taken.</FormFeedback>}
        </FormGroup>
        <div className="form-row">
          <FormGroup className="col-md-6">
            {hasLabel && <Label>Password</Label>}
            <Input
              placeholder={!hasLabel ? 'Password' : ''}
              value={password}
              onChange={({ target }) => setPassword(target.value)}
              type="password"
              invalid={password && !passwordValid}
            />
            <FormFeedback invalid>
              The password must be between 8 and 15 characters, and include at least 1 capital letter, 1 number, and 1 special character
            </FormFeedback>
          </FormGroup>
          <FormGroup className="col-md-6">
            {hasLabel && <Label>Confirm Password</Label>}
            <Input
              placeholder={!hasLabel ? 'Confirm Password' : ''}
              value={confirmPassword}
              onChange={({ target }) => setConfirmPassword(target.value)}
              type="password"
              invalid={password !== confirmPassword}
            />
            <FormFeedback invalid>
              The passwords must match
            </FormFeedback>
          </FormGroup>
        </div>
  
        <CustomInput
          id="customCheckTerms"
          label={
            <Fragment>
              I accept the <Link to="#!">terms</Link> and <Link to="#!">privacy policy</Link>
            </Fragment>
          }
          checked={isAccepted}
          onChange={({ target }) => setIsAccepted(target.checked)}
          type="checkbox"
        />
        <FormGroup>
          <Button color="primary" block className="mt-3" disabled={isDisabled}>
            Register
          </Button>
        </FormGroup>
        <Divider className="mt-4">or register with</Divider>
        <SocialAuthButtons />
      </Form>
    );
  }
  
};

export default withRedirect(RegistrationForm);
