////// Packages //////////////////
import React, { FC, useContext } from 'react';
import Typed from 'react-typed';
import { Link } from 'react-router-dom';
import { Row, Col, Button } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useSelector } from 'react-redux';

////// Local Components //////////

////// Global Components /////////
import Section from 'components/Section';
import Background from 'assets/img/backgrounds/bg-jelly-blue.jpg';
import ProductDemo from 'assets/img/backgrounds/product-demo.png'
import useSetting from 'store/hooks/settings/useSettings';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

const Banner: FC<any> = () => {
  const isDark = useSetting('isDark')

  return (
    <Section className="py-0 overflow-hidden" image={Background} position="center bottom" overlay>
      <Row className="justify-content-center align-items-center pt-8 pt-lg-10 pb-lg-9 pb-xl-0">
        <Col md={11} lg={8} xl={4} className="pb-7 pb-xl-9 text-center text-xl-left">
          <Button tag={Link} color="outline-danger" className="mb-4 fs--1 border-2x rounded-pill" to="#!">
            <span className="mr-2" role="img" aria-label="Gift">
              🎁
            </span>
            Become a pro
          </Button>
          <h1 className="text-white font-weight-light">
            Bring
            <Typed
              strings={['intelligence', 'flow', 'integration', 'ease', 'collaboration']}
              typeSpeed={40}
              backSpeed={50}
              className="font-weight-bold pl-2"
              loop
            />
            <br />
            to your business
          </h1>
          <p className="lead text-white opacity-75">
            Your core business is not tracking time, workload, progress, and billing.
            Let happin handle the heavy lifting of business administration and free you up to do what you do best.
            <br /><br />
            {`Let's make it happin.`}
          </p>
          <Link className="btn btn-outline-light border-2x rounded-pill btn-lg mt-4 fs-0 py-2" to="#!">
            Start managing your business flow with happin
            <FontAwesomeIcon icon="play" transform="shrink-6 down-1 right-5" />
          </Link>
        </Col>
        <Col xl={{ size: 7, offset: 1 }} className="align-self-end">
          <Link to="/" className="img-landing-banner">
            <img className="img-fluid" src={ProductDemo} alt="" />
          </Link>
        </Col>
      </Row>
    </Section>
  );
};

export default Banner;
