////// Packages //////////////////
import { Fragment, useEffect } from 'react';

////// Local Components //////////
import FooterStandard from './components/FooterStandard';
import Banner from './components/Banner';
import Processes from './components/Processes';
import Services from './components/Services';

////// Global Components /////////
import NavbarStandard from 'layouts/navbar/NavbarStandard';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const Landing = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);
  return (
    <Fragment>
      <NavbarStandard  />
      <Banner />
      <Processes />
      <Services />
      <FooterStandard />
    </Fragment>
  );
};


export default Landing;
