////// Packages //////////////////
import React from 'react';

////// Local Components //////////
import Process from './Process';
import SectionHeader from './SectionHeader';

////// Global Components /////////
import Section from 'components/Section';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Assets ////////////////
import illustration1 from '../../../assets/img/ui/1.svg';
import illustration2 from '../../../assets/img/ui/2.svg';
import illustration3 from '../../../assets/img/ui/3.svg';


const Processes = () => (
  <Section>
    <SectionHeader
      title="The solution for a fully integrated service business"
      subtitle="Using state of the art tools for data entry, insights, and collaboration, happin gives you the freedom to focus on your core business and operate at top efficiency."
    />
      <Process 
        icon="users"
        iconText='CONNECT'
        color='warning'
        title='Collaborative Tools'
        description={"Information silos belong in the 20th century. "
          + "Happin provides you collaborative tools to connect within your company, and to connect with your customers. " 
          + "And with searchable, integrated processes, you never have to worry about losing your valuable business information again."
        }
        image={illustration1}
      />
      <Process 
        icon="chart-bar"
        iconText='TRACK'
        color='success'
        title='Stay on top of it'
        description='Advanced data insights allow you to track your business health in a manageable setting. Dashboards and customized views allow you to see the information you need to keep your business rolling.'
        image={illustration2}
        inverse
      />
      <Process 
        icon="paint-brush"
        iconText='POLISH'
        color='info'
        title='Easy on the eyes'
        description={"Advanced information doesn't have to be clunky, dull and boring. "
          + "Happin is carefully designed for a modern experience using responsive tools, so your app experience feels fluid and seamless. " 
          + "And real-time communication means your data always stays up to date."
        }
        image={illustration3}
      />
  </Section>
);

export default Processes;
