////// Packages //////////////////
import React, { useState, useEffect, FC } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Alert, Button, Form, Row, Col, FormGroup, Input, CustomInput, Label } from 'reactstrap';

////// Local Components //////////
import SocialAuthButtons from './SocialAuthButtons';
import withRedirect from 'hoc/withRedirect';
import SpinnerLogo from 'components/SpinnerLogo';

////// Global Components /////////
import Divider from 'components/Divider';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { login } from 'store/thunks/login';
import useThunkDispatch from 'store/hooks/useThunkDispatch';

interface Props {
  setRedirect: (redirect: boolean)=>void;
  setRedirectUrl: (redirectUrl: string)=>void;
  hasLabel: boolean;
}

const LoginForm: FC<Props> = ({ setRedirect, setRedirectUrl, hasLabel=false }) => {
  // State 
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(true)
  const [isDisabled, setIsDisabled] = useState(true);
  const [ loading, setLoading ] = useState(false);
  const [error, setError ] = useState('')
  const dispatch = useThunkDispatch();


  // Handler
  const handleSubmit = async (event: any) => {
    event.preventDefault();
    const timer = setTimeout( () => { 
      setLoading(false);
      setError('The request timed out') 
    }, 5000);
    setLoading(true);
    const res = await dispatch(login(email, password));
    setLoading(false);
    clearTimeout(timer);
    if (res?.success && res.isLoggedIn) {
      toast.success(`Logged in as ${email}`);
      setError('')
      setRedirectUrl(`/home`);
      setRedirect(true);
      
    } else if (res?.verify) {
      setRedirectUrl(`/auth/confirm-mail/verify/${ encodeURIComponent(email) }`);
      setRedirect(true);
      setError('')
    } else {
      setError( JSON.stringify(res?.error, undefined, 2) || 'Something went wrong')
    }

  };

  useEffect(() => {
    setIsDisabled(!email || !password);
  }, [email, password]);

  if(loading) {
    return (
      <div className="mx-auto">
        <SpinnerLogo width={90} />
      </div> 
    )   
  } else {
    return (
      <Form onSubmit={handleSubmit}>
        { error && <Alert color='danger'>{ error }</Alert>}
        <FormGroup>
          {hasLabel && <Label>Email address</Label>}
          <Input
            placeholder={!hasLabel ? 'Email address' : ''}
            value={email}
            onChange={({ target }) => setEmail(target.value)}
            type="email"
          />
        </FormGroup>
        <FormGroup>
          {hasLabel && <Label>Password</Label>}
          <Input
            placeholder={!hasLabel ? 'Password' : ''}
            value={password}
            onChange={({ target }) => setPassword(target.value)}
            type="password"
          />
        </FormGroup>
        <Row className="justify-content-between align-items-center">
          <Col xs="auto">
            <CustomInput
              id="customCheckRemember"
              label="Remember me"
              checked={remember}
              onChange={({ target }) => setRemember(target.checked)}
              type="checkbox"
            />
          </Col>
          <Col xs="auto">
            <Link className="fs--1" to={`/auth/forgot-password`}>
              Forgot Your Password?
            </Link>
          </Col>
        </Row>
        <FormGroup>
          <Button color="primary" block className="mt-3" disabled={isDisabled}>
            Log in
          </Button>
        </FormGroup>
        <Divider className="mt-4">or log in with</Divider>
        <SocialAuthButtons />
      </Form>
    );
  }
};


export default withRedirect(LoginForm);
