////// Packages //////////////////
import React from 'react';
import { Row, Col } from 'reactstrap';
import className from 'classnames';

////// Local Components //////////
import CardService from './CardService';
import SectionHeader from './SectionHeader';

////// Global Components /////////
import Section from 'components/Section';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

const Services = () => (
  <Section bg="light" className="text-center">
    <SectionHeader
      title="Here's what's in it for you"
      subtitle="Things you will get right out of the box with happin."
    />
    <Row className="mt-6">
      <Col lg={4}>
        <CardService
          media={{ 
            icon: ['far', 'calendar-check'], 
            color: 'warning', 
            className: 'fs-4' 
          }}
          title='Timesheets'
          description="Timesheets, done the easy way. Get chargeable options from your business processes, and entry is as simple as click-and-drag on a calendar."
        />
      </Col>
      <Col lg={4} className={'mt-6 mt-lg-0'}>
      <CardService
          media={{ 
            icon: 'thumbtack', 
            color: 'primary', 
            className: 'fs-4' 
          }}
          title='Workflow Management'
          description={"Track your business processes. "
            + "Enter and track information about schedules, deadlines, and progress. "
            + "Store files and notes that are important to you in-line with your business information. "
            + "Use the real-time chat features to host focused discussions. "
            + "Use the integrated Customer Relationship Manager to track relationships with your valuable business partners."
          }
        />
      </Col>
      <Col lg={4} className={'mt-6 mt-lg-0'}>
        <CardService
          media={{ 
            icon: "dollar-sign", 
            color: 'success', 
            className: 'fs-4' 
          }}
          title='Contract, Expense, and Invoice Tracking'
          description={"Tracking finances is a taxing job for any services company. "
            + "Get the tools to track spending, progress velocity, and potential values inline with your process management. "
            + "Finance doesn't live in a silo, so your financial management should be as integral to your business process as schedules and communications."
          }
        />
      </Col>
    </Row>
  </Section>
);

export default Services;
