////// Packages //////////////////
import React, { FC, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { animateScroll } from 'react-scroll';

////// Local Components //////////

////// Global Components /////////
import Section from 'components/Section';
import IconGroup from 'components/icon/IconGroup';

////// CommonJS Helpers //////////
import { version } from '../../../environ';

////// Custom Hooks //////////////

////// Thunks ////////////////////



interface TitleProps {
  children: React.ReactNode
}

const FooterTitle: FC<TitleProps> = ({ children }) => <h5 className="text-uppercase text-white opacity-85 mb-3">{children}</h5>;

interface ListProps {
  list: {title: string, to: string}[]
}

const FooterList: FC<ListProps> = ({ list }) => (
  <ul className="list-unstyled">
    {list.map(({ title, to }, index) => (
      <li className="mb-1" key={index}>
        <Link className="text-600" to={to}>
          {title}
        </Link>
      </li>
    ))}
  </ul>
);

interface BlogItem {
  title: string;
  date: string;
  read: boolean;
  star: number;
}

interface BlogProps {
  list: BlogItem[]
}

const FooterBlogList: FC<BlogProps> = ({ list }) => (
  <ul className="list-unstyled">
    {list.map((blog, index) => (
      <li key={index}>
        <h5 className="fs-0 mb-0">
          <Link className="text-600" to="#!">
            {blog.title}
          </Link>
        </h5>
        <p className="text-600 opacity-50">
          {blog.date} &bull; {blog.read} read {blog.star && <span dangerouslySetInnerHTML={{__html: ''}} />}
        </p>
      </li>
    ))}
  </ul>
);

const FooterStandard = () => {
  return (
    <Fragment>
      <Section bg="dark" className="pt-8 pb-4">
        <div className="position-absolute btn-back-to-top cursor-pointer bg-dark" onClick={animateScroll.scrollToTop}>
          <FontAwesomeIcon icon="chevron-up" transform="rotate-45" className="text-600" />
        </div>
        <Row>
          <Col lg={4}>
            <FooterTitle>Our Mission</FooterTitle>
            <p className="text-600">
              Happin is the future of task management for small to medium sized services businesses who need agility, flexibility, intelligence, and
              efficiency in their work management and billing processes. By integrating the processes from ground-up, communication becomes seamless
              between the producer, manager, support, and administration levels of the organization. Always stay up to date when tracking your workload,
              employee availability, and project timelines and budgets.
            </p>
            <IconGroup className="mt-4" icons={[]} />
          </Col>
          <Col className="pl-lg-6 pl-xl-8">
            <Row className="mt-5 mt-lg-0">
              <Col xs={6} md={3}>
                <FooterTitle>Company</FooterTitle>
                <FooterList list={[]} />
              </Col>
              <Col xs={6} md={3}>
                <FooterTitle>Product</FooterTitle>
                <FooterList list={[]} />
              </Col>
              <Col className="mt-5 mt-md-0">
                <FooterTitle>From the Blog</FooterTitle>
                <FooterBlogList list={[]} />
              </Col>
            </Row>
          </Col>
        </Row>
      </Section>

      <section className=" bg-dark py-0 text-center fs--1">
        <hr className="my-0 border-600 opacity-25" />
        <div className="container py-3">
          <Row className="justify-content-between">
            <Col xs={12} sm="auto">
              <p className="mb-0 text-600">
                Design and function copyrighted by the builders of happin <span className="d-none d-sm-inline-block">| </span>
                <br className="d-sm-none" /> {new Date().getFullYear()} &copy;
              </p>
            </Col>
            <Col xs={12} sm="auto">
              <p className="mb-0 text-600">v{version}</p>
            </Col>
          </Row>
        </div>
      </section>
    </Fragment>
  );
};

export default FooterStandard;
